import { cleanup, render } from '@testing-library/preact';
import { expect } from 'chai';
import { HighlightText } from '../HighlightText';

afterEach(cleanup);

describe('Test HighlightText component:', () => {
  it('render no highlighting', async function () {
    const content = render(<HighlightText matchText="x">testing</HighlightText>);

    const spans = content.container.querySelectorAll('span');
    expect(spans.length, 'unhighlighted text').eq(0);
  });

  it('render highlighting', async function () {
    const content = render(<HighlightText matchText="t">testing</HighlightText>);

    const spans = content.container.querySelectorAll('span');
    expect(spans.length, 'highlighted text').eq(2);
  });
});
